[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

/* copy .has-error to .ng-invalid */
.ng-invalid.help-block,
.ng-invalid.control-label,
.ng-invalid.radio,
.ng-invalid.checkbox,
.ng-invalid.radio-inline,
.ng-invalid.checkbox-inline,
.ng-invalid.radio label,
.ng-invalid.checkbox label,
.ng-invalid.radio-inline label,
.ng-invalid.checkbox-inline label {
  color: #a94442;
}
.ng-invalid.form-control {
  border-color: #a94442;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
}
.ng-invalid.form-control:focus {
  border-color: #843534;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483;
}
.ng-invalid.input-group-addon {
  color: #a94442;
  background-color: #f2dede;
  border-color: #a94442;
}


label {
  font-weight: normal;
}

.katex-display {
  display: inline-block;
  margin: 0;
  text-align: inherit;
}

.input-view input[name="decimalPrecisionNumber"] {
  min-width: 40px;
  -moz-appearance:textfield;
}

.input-view input[name="decimalPrecisionNumber"]::-webkit-outer-spin-button,
.input-view input[name="decimalPrecisionNumber"]::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.solution-view p {
  margin-top: 0.5em;
}

.solution-step {
  margin: 1em 0;
}

.matrix-table {
  border: 2px solid #555;
  border-radius: 10px/40px;
  padding: 8px;
  display: inline-block;
  border-top: none;
  border-bottom: none;
}

.augmented-matrix-table td:last-child {
  border-left: 1px dashed #555;
}

.matrix-table input {
  min-width: 100%;
  border-radius: 0;
  padding: 2px 5px;
  height: 26px;
  text-align: right;
}

.matrix-table > tbody > tr > td {
  min-width: 50px;
  padding: 2px;
}
